<template>
	<div class="saleedi-order-ent">
		<van-collapse v-model="activeNames" class="ent-collapse">
			<van-collapse-item  v-for="(item, index) in orderEntData" :key="index" :name="index">
				<div slot="title" class="ent-collapse-ent">{{item.name}}</div>
				<div v-for="(rdcItem, rdcIndex) in item.center" :key="rdcIndex" class="ent-collapse-rdc">{{rdcItem.name}}</div>
			</van-collapse-item>
		</van-collapse>
		<div class="order-ent-bottom">
			<van-button class="add-btn" type="primary" round @click="addEntClick">+ 添加订货企业</van-button>
		</div>
	</div>
</template>

<script>
// let orderEnt = require('@/json/companyList.json')
export default {
	name: 'orderEnt',
	data () {
		return {
			activeNames: [],
			orderEntData: []
		}
	},
	created () {
		this.getCompany()
	},
	methods: {
		addEntClick () {
			this.$router.push({
				path: '/bindEnt',
				query: {
					notFirst: true
				}
			})
		},
		getCompany () {
			this.$fxHttp.requestPost({
				url: '/getOrderCompany'
			}).then(res => {
				this.getAllRDC(res[0].name).then(rdcRes => {
					res[0].center = rdcRes
					this.orderEntData = res
				})
			})
		},
		getAllRDC (companyName) {
			return new Promise(resolve => {
				const params = {
					companyName: companyName
				}
				this.$fxHttp.requestPost({
					url: '/getAllDC',
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		}
	}
}
</script>
